.buttonWrapper {
  display: inline-block;
  width: fit-content;
  box-sizing: border-box;
  padding: 3px 8px;
  border-radius: 8px;

  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: capitalize;

  color: #ffffff;
  display: flex;
  align-items: center;
}
