.planCard {
  position: relative;
  width: 100%;
  background: #1d1c1c;
  // background: white;
  border-radius: 20px;
  padding: 25px;
  p {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-transform: capitalize;
    color: #ffffff;
    opacity: 0.5;
    text-align: left;
    margin: 20px 0;
  }

  span {
    display: block;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-transform: capitalize;
    text-align: left;
  }
  .planBar {
    background-color: #f3b412;
    width: fit-content;
    position: absolute;
    left: 0;
    top: 0;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-transform: capitalize;
    color: #000000;
    padding: 2px 8px;
  }
  .profitDetail {
    width: 100%;
  }

  .getBnb {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    > p {
      margin: 0;
      text-align: left;
    }
    > span {
      font-weight: 700;
      font-size: 16px;
      line-height: 16px;
      text-transform: capitalize;
      color: #f3b412;
      margin-bottom: 0;
    }
  }
  .planButton {
    margin-top: 10px;
    width: 100%;
    background-color: #f3b412;
    border-radius: 8px;
    padding: 15px 0px;
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    text-align: center;
    text-transform: capitalize;

    color: #151414;
  }
}
input {
  background: #151414 !important;
  box-shadow: none;
  border: none !important;
  height: 50px;
}
.blueFont {
  color: #12d8f3;
}
.yellowFont {
  color: #f3b412;
}
.blueBg {
  background-color: #12d8f3 !important;
}

@media (max-width: 765px) {
  .planCard {
    p {
      margin: 15px 0;
    }
    span {
      display: block;
      font-weight: 700;
      font-size: 28px;
      line-height: 28px;
      text-transform: capitalize;
      text-align: left;
    }
  }
}
