.balanceCardWrapper {
  // height: 100%;
  width: 100%;
  background: #1d1c1c;
  border-radius: 20px;
  padding: 15px 0;
  position: relative;
  text-align: left;
  padding-left: 20px;
  overflow-x: hidden;

  .colorBar {
    width: 8px;
    height: 60%;
    background: #f3b412;
    position: absolute;
    left: 0;
    top: 20%;
  }
  .cardContent {
    > h2 {
      color: white;
      font-size: 18px;
      line-height: 20px;
      margin-bottom: 10px;
      > span {
        display: inline-block;
        background: #f3b412;
        color: #222222;
        font-weight: 700;
        padding: 2px;
        font-size: 16px;
      }
    }
    > p {
      font-style: normal;
      font-weight: 700;
      font-size: 30px;
      line-height: 36px;
      text-transform: capitalize;
      color: #f39c12;
      margin-bottom: 0;
    }
  }
}
