.stakeCard {
  width: 100%;
  background: #1d1c1c;
  padding: 20px;
  border-radius: 15px;
  .innerHeader {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
    padding: 5px;
    padding-right: 30px;
    .bar {
      width: fit-content;
      //   position: absolute;
      //   left: 0;
      //   top: 0;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      text-transform: capitalize;
      color: #000000;
      margin-left: -10px;
      margin-top: -10px;
      vertical-align: middle;

      > p {
        display: inline-block;
        background-color: #f3b412;
        margin-bottom: 0;
      }

      > span {
        display: inline-block;
        background: #1d1c1c;
        color: #f3b412;
        height: 100%;
        padding-left: 4px;
      }
    }
    > p {
      margin-bottom: 0;
      font-size: 16px;
      line-height: 16px;
      text-transform: capitalize;
      color: #ffffff;
      opacity: 0.5;
    }
  }
  .planDetail {
    margin: 20px auto;
    padding: 5px 25px;
    p {
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
      text-transform: capitalize;
      color: #ffffff;
      opacity: 0.5;
      text-align: left;
      margin: 0;
      margin-top: 2px;
    }

    span {
      display: block;
      font-weight: 700;
      font-size: 20px;
      line-height: 20px;
      text-transform: capitalize;
      text-align: left;
      margin-bottom: 16px;
      color: #f3b412;
      margin: 0;
    }
  }
  .progress {
    // padding: 0 25px;
    width: 100%;
    background-color: #3c3c3c;
    overflow: hidden;
    border-radius: 20px;
    .inner {
      height: 5px;
      width: 10%;
      background-color: #f3b412;
    }
  }
}
